<template>
  <div class="personMsg">
    <div>
      <el-input :value="user.login_id" :disabled="true">
        <template slot="prepend">{{user.role_id==1 ? '' : ''}}</template>
      </el-input>
    </div>
    <div>
      <el-input :value="user.name" :disabled="true">
        <template slot="prepend">{{''}}</template>
      </el-input>
    </div>
    <div>
      <el-input :value="user.gender == 0 ? '' : ''" :disabled="true">
        <template slot="prepend">{{''}}</template>
      </el-input>
    </div>
    <div>
      <el-input :value="user.phone" :disabled="true">
        <template slot="prepend">{{''}}</template>
      </el-input>
    </div>
    <div>
      <el-input v-if="user.role_id==1" :value="user.grade_name" :disabled="true">
        <template slot="prepend">{{''}}</template>
      </el-input>
    </div>
    <div>
      <el-input v-if="user.role_id!=1" :value="user.department_name" :disabled="true">
        <template slot="prepend">{{''}}</template>
      </el-input>
    </div>
    <div>
      <el-input v-if="user.role_id==1" :value="user.major_name" :disabled="true">
        <template slot="prepend">{{''}}</template>
      </el-input>
    </div>
    <div>
      <el-input v-if="user.role_id==1" :value="user.classes" :disabled="true">
        <template slot="prepend">{{''}}</template>
      </el-input>
    </div>
    <div>
      <el-input
        v-if="user.role_id==1"
        :value="user.student_status == 0 ? '' : ''"
        :disabled="true"
      >
        <template slot="prepend">{{''}}</template>
      </el-input>
    </div>
    <div>
      <el-input
        :value="user.role_id == 1 ? user.person_status == 0 ? '' : user.person_status == 1 ? '' : user.person_status == 2 ? '' : '' : user.person_status == 0 ? '' : ''"
        :disabled="true"
      >
        <template slot="prepend">{{''}}</template>
      </el-input>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  computed: {
    ...mapState("loginUser", ["user"])
  }
};
</script>

<style scope>
.el-input-group__prepend {
  color: #409eff;
  width: 57px;
  text-align: center;
}
</style>