<template>
  <div class="stuForm">
    <el-dialog
      title=""
      :visible.sync="addFormVisible"
      :before-close="handleClose"
      @closed="reset"
      :modal-append-to-body="false"
      width="500px"
      top="0"
    >
      <el-form :model="addForm" status-icon :rules="rules" ref="addForm" label-width="100px">
        <el-form-item label="" prop="login_id">
          <el-input v-model="addForm.login_id"></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="addForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="gender">
          <el-select v-model="addForm.gender" placeholder="">
            <el-option label="" :value="0"></el-option>
            <el-option label="" :value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="addForm.phone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="grade_id">
          <el-select v-model="addForm.grade_id" placeholder="">
            <el-option
              v-for="item in gradeList"
              :key="item.grade_id"
              :label="item.grade_name"
              :value="item.grade_id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="major_id">
          <el-select v-model="addForm.major_id" placeholder="">
            <el-option
              v-for="item in majorList"
              :key="item.major_id"
              :label="item.major_name"
              :value="item.major_id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="classes">
          <el-input v-model="addForm.classes"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click.stop="submitForm('addForm')"></el-button>
          <el-button @click.stop="reset"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  props: ["addFormVisible", "addForm"],
  data() {
    return {
      rules: {
        login_id: [{ required: true, message: "", trigger: "blur" }],
        name: [{ required: true, message: "", trigger: "blur" }],
        phone: [
          { required: false, message: "", trigger: "blur" }
        ],
        email: [{ required: false, message: "", trigger: "blur" }],
        gender: [{ required: true, message: "", trigger: "change" }],
        grade_id: [
          { required: true, message: "", trigger: "change" }
        ],
        major_id: [
          { required: true, message: "", trigger: "change" }
        ],
        classes: [{ required: true, message: "", trigger: "blur" }]
      }
    };
  },
  computed: {
    ...mapState("grade", ["gradeList"]),
    ...mapState("major", ["majorList"])
  },
  methods: {
    ...mapActions("student", ["addStudents"]),
    handleClose() {
      this.$emit("close", false);
    },
    reset() {
      this.$refs.addForm.resetFields();
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        const arr = [this.addForm];
        console.log(arr);
        this.addStudents(arr).then(res => {
          this.$message({
              message: res.msg,
              type: "success",
              duration: 2000,
              center: true
          });
        });
        this.handleClose();
      });
    }
  }
};
</script>
